<template>
  <div :id="this.id" class="image-view">
    <h1>{{ info.title }}, {{ sequencePlayer.getCurrentIndex() }} / {{ sequencePlayer.getLastIndex() }}</h1>
    <img class="gallery-thumb" v-bind:src="img">
    <div class="control-panel">
      <BlushButton v-on:click="this.firstB()">First</BlushButton>
      <BlushButton v-on:click="this.prevB()">Prev</BlushButton>
      <BlushButton v-on:click="this.playB()"><span v-if="paused">Play</span><span v-else>Pause</span></BlushButton>
      <BlushButton v-on:click="this.nextB()">Next</BlushButton>
    </div>
    <p>Image: {{ img }}</p>
    <p>{{ description }}</p>
    <div class="sequence-thumbs">
      <img class="thumb" v-for="item in allImages" v-bind:src="item">
    </div>
  </div>
</template>

<script lang="ts">
import {Options, setup, Vue} from 'vue-class-component'
import axios from 'axios'
import util from '../util'
import SequencePlayer from '../SequencePlayer'
import BlushButton from "@/components/BlushButton.vue";

@Options({
  components: {
    BlushButton
  },
  props: {
  },
  methods: {
    log (msg: any) {
      console.log(msg)
    },
    async play() {
      await util.wait(200)
      if (this.sequencePlayer.getCurrentIndex() == this.sequencePlayer.getLastIndex()) {
        await util.wait(1000) // Stay extra on the last image
        if (this.paused) return
        this.firstB()
        await util.wait(800)
        if (this.paused) return
      }
      this.nextB()
      await util.wait(800)
      if (!this.paused) this.play()
    },
    playB() {
      this.paused = !this.paused
      if (!this.paused) this.play()
    },
    firstB() {
      this.img = this.sequencePlayer.goto(0).get()
    },
    prevB() {
      this.img = this.sequencePlayer.prev().get()
    },
    nextB() {
      this.img = this.sequencePlayer.next().get()
    }
  },
  async beforeCreate() {
    console.log(this.$route.params.id)
    this.id = this.$route.params.id
    let response = await axios.get(util.baseUrl() + '/' + this.id + '/info.json')
    this.info = response.data
    this.description = this.info.description
    this.sequencePlayer = new SequencePlayer(this.id, this.info)
    this.img = this.sequencePlayer.get()
    this.allImages = this.sequencePlayer.getAll()
  },
  setup() {
    console.log("SETUP")
  }
})
export default class ImageView extends Vue {
  id!: string
  img = ''
  description = ''
  info: any = {
    title: "",
    firstImage: ""
  }
  sequencePlayer: SequencePlayer = new SequencePlayer("none", this.info)
  allImages: string[] = []
  paused: boolean = true
}
</script>

