<template>
  <div :id="this.id" class="gallery">
    <h1>Gallery</h1>
    <div class="thumbs">
      <div v-for="item in allThumbs">
        <router-link :to="'/' + item.id">
          <img class="thumb" v-bind:src="item.thumb">
        </router-link>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import {Options, setup, Vue} from 'vue-class-component'
import axios from 'axios'
import util from '../util'
import SequencePlayer from '../SequencePlayer'
import BlushButton from "@/components/BlushButton.vue";

@Options({
  components: {
    BlushButton
  },
  props: {
  },
  methods: {
    async getThumb(id: string) {
      let response = await axios.get(util.baseUrl() + '/' + id + '/info.json')
      let info = response.data
      return `${util.baseUrl()}/${id}${info.thumb}`
    }
  },
  async beforeCreate() {
    let response = await axios.get(util.baseUrl() + '/gallery-info.json')
    this.sequences = response.data.sequences
    for (let id of this.sequences) {
      this.allThumbs.push({id: id, thumb: await this.getThumb(id)})
    }
  }
})
export default class Gallery extends Vue {
  sequences: string[] = []
  allThumbs: string[] = []
}
</script>

